body{
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
    background-color: black;
}

.top-banner{
    height: 3em;
    top: 0 !important;
    margin-top: -1em;
    border-bottom: 1px solid rgba(245, 245, 245, 0.5);
}

.top-banner marquee{
    color: whitesmoke;
    padding-top: 18px !important;
}

.board-row{
    clear: both;/* no element appears to the left or right of the floated element*/
    display: table; /*element to behave like a table*/
    content: "";
}

.nextPlayer{
    margin-bottom: 10px ;
    color: whitesmoke;
    padding-bottom: 10px;
}

/* the individual button that will display X or O */
.square{
    background: black;
    border: 3px solid whitesmoke;
    float: right;
    font-size: 70px;
    font-weight: bold;
    line-height: 25px;
    height: 75px;
    width: 75px;
    margin-right: -1px;
    margin-top: -5px;
    padding: 0 ;
    text-align: center;
    color: whitesmoke;
}

.square:focus{
    outline: invert;
}

.game{
    /* the items to take the same length and be displayed horizontally */
    display: flex;
    flex-direction: row;
    /* align-items: center; */
    justify-content: center;
    /* vertical-align:middle; */
    /* position: relative; */
    /* top: 150%; */
    transform: translateY(90%);
}

.game-info{
    margin-left: 100px;
    color: white;
    margin-top: 0;
    width: 150px;
}

.winner-counter{
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    justify-content: space-between;
}

/* mobile styles for responsive view */

/* for medium devices, up to 480px */
@media only screen and (max-width:480px){

    .game{
        display: flex;
        flex-direction: column;
        align-items: center;
        transform: translateY(35%);
    }

    .game-info{
        margin-left: 0 !important;
        width: 200px;
    }

    .winner-count-header{
        margin-top: 15px;
    }

    .winner-counter{
        margin-top: 5px;
    }
}